<template>
  <AppCard
    basis="350px"
    :title="'Toвар #' + (props.index + 1)"
  >
    <div class="form">
      <FormKitSchema :schema="productSchema" />
    </div>
    <template #actions>
      <Button
        v-if="!props.readonly"
        severity="danger"
        icon="pi pi-trash"
        @click="emit('delete')"
      />
    </template>
  </AppCard>
</template>

<script setup lang="ts">
import AppCard from '@/components/markup/AppCard.vue';
const props = defineProps<{index: number; readonly: boolean}>();
const emit = defineEmits(['delete']);

const productSchema = [
  {
    $formkit: 'primeInputText',
    name: 'url',
    label: 'Ссылка на товар',
    help: props.readonly ? '' : 'Пример: https://example.com/product?id=42',
    validation: 'required',
    _disabled: props.readonly,
  },
  {
    $formkit: 'primeInputNumber',
    name: 'priceForOne',
    label: 'Цена за единицу товара',
    suffix: ' PLN',
    minFractionDigits: 2,
    maxFractionDigits: 2,
    validation: 'required|min:0.01|max:1000000000',
    _disabled: props.readonly,
  },
  {
    $formkit: 'primeInputNumber',
    name: 'count',
    label: 'Количество товара',
    validation: 'required|min:1|max:1000000000',
    _disabled: props.readonly,
  },
];
</script>

<style module lang="scss">
.root {
  flex-basis: 350px;
  flex-shrink: 1;
}
</style>
